// Polyfills
import "abortcontroller-polyfill/dist/polyfill-patch-fetch";

import { useTheme } from "../src/theme";
import {
  createEmotionCache,
  createEmotionCacheRtl,
} from "../src/createEmotionCache";
import { GlobalProvider, useGlobal } from "../src/context";
import { useRouter } from "next/router";
import { useEffect, useRef, useState } from "react";

// Client-side cache, shared for the whole session of the user in the browser.
const clientSideEmotionCache = createEmotionCache();
const clientSideEmotionCacheRtl = createEmotionCacheRtl();

interface MyAppProps extends AppProps {
  emotionCache?: EmotionCache;
}

const AppMeta = () => {
  const t = useTranslations();
  return (
    <Head>
      <title>{t("seo-home-page-title")}</title>
      <meta name="description" content={t("seo-home-page-description")} />
      <meta name="viewport" content="initial-scale=1, width=device-width" />

      <meta property="og:type" content="website" />
      <meta property="og:title" content={t("seo-home-page-title")} />
      <meta
        property="og:description"
        content={t("seo-home-page-description")}
      />
      <meta
        property="og:image"
        content="https://assets.flightscanner.com/assets/images/hp-image.jpeg"
      />

      <meta name="twitter:creator" content="flightscanner" key="twhandle" />
      <meta
        name="twitter:image"
        content="https://assets.flightscanner.com/assets/images/hp-image.jpeg"
      />

      {/* <script data-ad-client="ca-pub-7356498723652153" async src="https://pagead2.googlesyndication.com/pagead/js/adsbygoogle.js"></script> */}
    </Head>
  );
};


const LocaleProvider = ({ children }: any) => {
  const { dateLocaleFile } = useGlobal();

  return (

    <LocalizationProvider
    dateAdapter={AdapterDateFns}
    locale={dateLocaleFile}
  >
    {children}
    </LocalizationProvider>
  )
}

const App = (props: MyAppProps) => {
  const { Component, emotionCache, pageProps } = props;
  const theme = useTheme();
  const gclid = useRef<string | undefined>();
  const { events, query, replace, pathname, isFallback } = useRouter();

  useEffect(() => {
    if (query?.gclid) {
      gclid.current = query.gclid as string;
    }
  }, [query]);

  const routeChangeComplete = React.useCallback(() => {
    if (!query?.gclid && gclid.current) {
      replace({
        pathname,
        query: {
          ...query,
          gclid: gclid.current,
        },
      });
    }
  }, [query]);

  // const routeChangeStart = React.useCallback(() => {
  //   if (query?.gclid) {
  //     gclid.current = query.gclid as string;
  //   }
  // }, [query]);

  useEffect(() => {
    events.on("routeChangeComplete", routeChangeComplete);
    // events.on("routeChangeStart", routeChangeStart);
    return () => {
      events.off("routeChangeComplete", routeChangeComplete);
      // events.off("routeChangeStart", routeChangeStart);
    };
  }, [events, query]);

  if (isFallback) {
    return <p>Loading...</p>;
  }

  return (
    <CacheProvider
      value={
        emotionCache
          ? emotionCache
          : theme.direction === "rtl"
          ? clientSideEmotionCacheRtl
          : clientSideEmotionCache
      }
    >
      <NextIntlProvider messages={pageProps.messages} locale="en">
        {/* <NextIntlProvider
        messages={pageProps.messages}
        onError={onError}
        getMessageFallback={getMessageFallback}
      > */}
        <Script
          src="https://www.googletagmanager.com/gtag/js?id=G-2EGF7DJXW2"
          strategy="afterInteractive"
        />
        <Script id="google-analytics" strategy="afterInteractive">
          {`
            window.dataLayer = window.dataLayer || [];
            function gtag(){dataLayer.push(arguments);}
            gtag('js', new Date());

            gtag('config', 'G-2EGF7DJXW2');
        `}
        </Script>
        <ThemeProvider theme={theme}>
          {/* CssBaseline kickstart an elegant, consistent, and simple baseline to build upon. */}
          <CssBaseline />
          <GlobalProvider>
            <LocaleProvider>
              <AppMeta />
              <Component {...pageProps} />
            </LocaleProvider>
          </GlobalProvider>
        </ThemeProvider>
      </NextIntlProvider>
    </CacheProvider>
  );
};

export default App;
