export const languageByLocale = languages.reduce((acc: any, curr: any) => {
  acc[curr.id.split("-")[0]] = curr;
  return acc;
}, {}) as any;

const currencies = Object.keys(currenciesMap)
  .map((currencyKey) => currenciesMap[currencyKey])
  .concat(allCurrencies);

export type UserContextType = {
  currency: any;
  languages: any;
  currencies: any;
  setCurrency: any;
  setCurrencyByLocale: any;
  searchState: any;
  setSearchState: any;
  dateLocaleFile: any;
};

export const GlobalContext = createContext<UserContextType>({
  currency: null,
  languages: null,
  currencies: null,
  setCurrency: null,
  setCurrencyByLocale: null,
  searchState: null,
  setSearchState: null,
  dateLocaleFile: null,
});

const cookieName = "selected_currency";

export const inOneYearDate = () =>
  new Date(new Date().setFullYear(new Date().getFullYear() + 1));

export const GlobalProvider: FC = ({ children }) => {
  const { locale } = useRouter();
  const [currency, setCurrency] = useState(
    (Cookies.get(cookieName)
      ? currenciesMap[Cookies.get(cookieName) as string]
      : locale && currenciesMap[languageByLocale[locale]?.currency]) ||
      currenciesMap["EUR"]
  );
  const [searchState, setSearchState] = useState({});

  const handleSetCurrency = useCallback((newCurrency: any) => {
    Cookies.set("selected_currency", newCurrency.code, {
      expires: inOneYearDate(),
    });
    setCurrency(newCurrency);
  }, []);

  const handleCurrencyByLocale = useCallback((newLocale: any) => {
    if (!Cookies.get(cookieName)) {
      const newCurency = currenciesMap[languageByLocale[newLocale]?.currency];
      Cookies.set("selected_currency", newCurency.code, {
        expires: inOneYearDate(),
      });
      setCurrency(newCurency);
    }
  }, []);

  const handleSearchState = useCallback((newState: any) => {
    setSearchState((prevState) => ({
      ...prevState,
      ...newState,
    }));
  }, []);

  const [dateLocaleFile, setDataLocaleFile] = useState<
    string | object | undefined
  >();

  useEffect(() => {
    const lowerCaseLocale = locale?.toLowerCase();

    const importLocaleFile = async () => {
      const fullLocale = (locale: string) => {
        if (lowerCaseLocale === "cn") {
          return "zh-CN";
        }
        return locale;
      };

      const dnsLocale = (
        await import(`date-fns/locale/${fullLocale(locale as string)}/index.js`)
      ).default;
      setDataLocaleFile(dnsLocale);
    };

    // Update RTL
    if (lowerCaseLocale === "he" || lowerCaseLocale === "ar") {
      document.dir = "rtl";
    } else {
      document.dir = "ltr";
    }

    if (lowerCaseLocale === "en") {
      setDataLocaleFile(undefined);
    } else {
      importLocaleFile();
    }
  }, [locale]);

  const contextValue: UserContextType = useMemo(
    () => ({
      currency,
      languages,
      currencies,
      setCurrency: handleSetCurrency,
      setCurrencyByLocale: handleCurrencyByLocale,
      searchState,
      setSearchState: handleSearchState,
      dateLocaleFile,
    }),
    [
      currency.code,
      searchState,
      handleSetCurrency,
      handleCurrencyByLocale,
      handleSearchState,
      dateLocaleFile
    ]
  );

  return (
    <GlobalContext.Provider value={contextValue}>
      {children}
    </GlobalContext.Provider>
  );
};

export const useGlobal = () => useContext(GlobalContext);
